.scrollToTopBtn {
  cursor: pointer;
  opacity: .8;
  background-color: #9bb537;
  border: none;
  padding: 10px;
  transition: opacity .3s;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.show-btn {
  display: block;
}

.scrollToTopBtn:hover {
  opacity: 1;
}

.scrollToTopBtn img {
  width: 30px;
  height: 30px;
  object-fit: contain;
}

@media (max-width: 768px) {
  .scrollToTopBtn {
    bottom: 10px;
    right: 10px;
  }

  .scrollToTopBtn img {
    width: 25px;
    height: 25px;
  }
}

@media (max-width: 480px) {
  .scrollToTopBtn {
    bottom: 5px;
  }
}

